.row-container{
margin-left: 10px;
color: white;
}

.row-header{
    margin-left: 20px;
}

.row-cards{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding:20px;
    cursor: pointer;
}

.row-cards::-webkit-scrollbar{
    display: none;
}

.movie-card{
    object-fit: contain;
    width: 100%;
    max-height: 200px;
    margin-bottom: 10px;
    transition: transform 450ms;
    border-radius: 10px;
    margin-right: 15px;
}

.movie-card-large{
    object-fit: contain;
    width: 100%;
    max-height: 200px;
    margin-bottom: 10px;
    transition: transform 450ms;
}

.movie-card:hover{
    transform: scale(1.08);
}